import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    questions: [],
    correctQuestions: [],
    wrongQuestions: [],
    selectedQuestionIndex: 0,
    nextActive: true
  },
  getters: {
    allQuestions: state => state.questions,
    correctQuestions: state => state.correctQuestions,
    wrongQuestions: state => state.wrongQuestions,
    selectedQuestion: state => state.questions[state.selectedQuestionIndex],
    nextActive: state => state.nextActive
  },
  actions: {
    async fetchQuestions({ commit }) {
      await axios.get("/api/questions").then(response => {
        commit("populateQuestions", response.data);
      });
    },
    correctAnswer({ commit }, question) {
      commit("addCorrectQuesiton", question);
    },
    wrongAnswer({ commit }, question) {
      commit("addWrongQuestion", question);
    },
    nextQuestion({ commit }) {
      commit("nextQuestion");
    }
  },
  mutations: {
    populateQuestions: (state, data) => (state.questions = data),
    addCorrectQuesiton: (state, question) =>
      state.correctQuestions.push(question),
    addWrongQuestion: (state, question) => state.wrongQuestions.push(question),
    nextQuestion: state => {
      if (state.selectedQuestionIndex == state.questions.length - 2) {
        state.nextActive = false;
      }
      if (!(state.selectedQuestionIndex == state.questions.length - 1)) {
        state.selectedQuestionIndex += 1;
      }
    }
  }
});
