import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Descipline from "../views/Descipline.vue";
import Exam from "../views/Exam.vue";
import Finish from "../views/Finish.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/descipline",
    name: "descipline",
    component: Descipline
  },
  {
    path: "/exam",
    name: "exam",
    component: Exam
  },
  {
    path: "/finish",
    name: "finish",
    component: Finish
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
